import '@babel/polyfill'

export const checkoutLoginToggleInit = () => {
  const toggleLink  = document.querySelector('.p-checkout a.showlogin');

  if (toggleLink) {
    // create copy of node to avoid click event error in scrollDown.js
    let copy = toggleLink.cloneNode(true);
    toggleLink.closest('.woocommerce-info').replaceChild(copy, toggleLink);

    const newToggleLink  = document.querySelector('.p-checkout a.showlogin');
    newToggleLink.addEventListener('click', (e) => { 
      const form = document.querySelector('.woocommerce-form-login-wrapper').classList.toggle('js-show');
    });
  }
}




