import '@babel/polyfill'
import 'nodelist-foreach-polyfill'
import { ElementObserver } from 'viewprt'

const isInViewPlay = () => {
  const inViewElements = document.querySelectorAll('.play-in-view')

  if (inViewElements != null) {
    inViewElements.forEach((inViewElement, index) => {
      const playVideosObserver = ElementObserver(inViewElement, {
        onEnter (element, viewport) {
          element.querySelector('video').play();
        },
        once: false,
        offset: -200
      })
    })
  }
}

export default isInViewPlay
