import '@babel/polyfill'
import StickySidebar from 'sticky-sidebar'

export const stickySidebarInit = () => {
  const checkoutSidebar = document.querySelector('.p-checkout__sidebar')
  if (checkoutSidebar) {
    const sidebar = new StickySidebar(checkoutSidebar, {
      topSpacing: 0,
      bottomSpacing: 0,
      containerSelector: '.p-checkout__inner',
      innerWrapperSelector: '.sidebar__inner',
      resizeSensor: true,
      stickyClass: 'is-affixed',
      minWidth: 768
    })

    console.log(sidebar)
  }
}
