import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import className from 'classnames'
import qs from 'qs'

class Dropdown extends Component {
  state = {
    isActive: false
  };

  setRef = (node) => {
    this.ref = node
  }

  componentDidMount () {
    document.addEventListener('mousedown', this.handleClickOutside)
  }

  componentWillUnmount () {
    document.removeEventListener('mousedown', this.handleClickOutside)
  }

  handleClickOutside = (event) => {
    if (this.ref && !this.ref.contains(event.target)) {
      this.setState({
        isActive: false
      })
    }
  }

  onClick = () => {
    this.setState(state => ({
      isActive: !state.isActive
    }))
  }

  onChange = id => {
    const { onChange } = this.props

    if (onChange) {
      onChange(id)
    }

    this.setState({
      isActive: false
    })
  };

  render () {
    const { isActive } = this.state
    const { options, value } = this.props

    const placeholder = options.find(el => el.variation_id === value)

    return (
      <div className={className('dropdown', { dropdown_active: isActive })} ref={this.setRef}>
        <div className='dropdown__head' onClick={this.onClick}>
          <div className='dropdown__label'>
            {placeholder.attributes.attribute_pa_quantity} - <span dangerouslySetInnerHTML={{ __html: placeholder.price_html }}></span>
          </div>
        </div>
        <div className='dropdown__options'>
          {options.map((el, index) => (
            <div
              key={index}
              className={className('dropdown__option', {
                dropdown__option_selected: el.variation_id === value
              })}
              onClick={() => this.onChange(el.variation_id)}
            >
              {el.attributes.attribute_pa_quantity} - <span dangerouslySetInnerHTML={{ __html: el.price_html }}></span>
            </div>
          ))}
        </div>
      </div>
    )
  }
}

class Product extends Component {
  state = {
    product: window.product_json,
    flavors: window.flavors_json,
    variations: window.variations_json,
    labels: window.labels,
    products: []
  }

  setProduct = (flavor, variation, status = 'change') => {

    if(status === 'change') {
      this.setState(state => ({
        products: state.products.map(product => product.flavor === flavor ? { flavor, variation } : product)
      }))
    } else if (status === 'toggle') {

      const product = this.state.products.find(product => product.flavor === flavor)

      if(this.state.products.length === 1 && product) {
        return
      }

      if(product) {
        this.setState(state => ({
          products: state.products.filter(product => product.flavor !== flavor ).map(product => {
            const defaultVariation = window.variations_json.find(el => (el.attributes.attribute_pa_flavors === product.flavor) && (el.attributes.attribute_pa_quantity === window.product_json.default_attributes.pa_quantity)).variation_id

            return {
              ...product,
              variation: defaultVariation
            }
          })
        }))
      } else {

        const defaultVariation = window.variations_json.find(el => (el.attributes.attribute_pa_flavors === flavor) && (el.attributes.attribute_pa_quantity === window.product_json.default_attributes.pa_quantity)).variation_id

        const products = [
          ...this.state.products,
          {
            flavor,
            variation: defaultVariation
          }
        ].map(product => {
          const firstVariation = window.variations_json.find(el => (el.attributes.attribute_pa_flavors === product.flavor)).variation_id

          return {
            ...product,
            variation: firstVariation
          }
        })

        this.setState({
          products
        })
      }
    } else if (status === 'default') {
      const defaultVariation = window.variations_json.find(el => (el.attributes.attribute_pa_flavors === flavor) && (el.attributes.attribute_pa_quantity === window.product_json.default_attributes.pa_quantity)).variation_id

      const products = [
        ...this.state.products,
        {
          flavor,
          variation: defaultVariation
        }
      ]

      this.setState({
        products
      })
    }
  }

  componentDidMount() {
    if(window.location.search) {
      const params = qs.parse(window.location.search.substr(1))

      if(params.hasOwnProperty('attribute_pa_flavors')) {
        this.setProduct(params.attribute_pa_flavors, null, 'default')
      } else {
        this.setDefaults()
      }

    } else {
      this.setDefaults()
    }
  }

  setDefaults = () => {
    const defaultFlavor = window.product_json.default_attributes.pa_flavors
    this.setProduct(defaultFlavor, null, 'default')
  }

  render () {
    const { product, flavors, variations, products, labels } = this.state
    const selectedFlavors = products.map(i => i.flavor)
    const selectedVariations = products.map(i => i.variation)
    const selectedMinAmount = window.min_amount

    const total = variations.filter(variation => selectedVariations.includes(variation.variation_id)).reduce((a, i) => { return i.display_price + a }, 0)

    return (
      <div className="p-product">
        <div className="p-product__inner">
          <div className="p-product__part">
            <div className="p-product__gallery">
              <div className="p-product__gallery-line" style={products.length === 1 ? {backgroundColor: flavors.find(i => i.data.slug === products[0].flavor).color_shade_1} : {}}></div>
              <div className="p-product__gallery-line" style={products.length === 1 ? {backgroundColor: flavors.find(i => i.data.slug === products[0].flavor).color_shade_2} : {}}></div>
              <div className="p-product__gallery-line" style={products.length === 1 ? {backgroundColor: flavors.find(i => i.data.slug === products[0].flavor).color_shade_3} : {}}></div>

              <div className="p-product__gallery-inner">
                <div className="p-product__gallery-images" style={products.length ? { transform: `translateX(${products.length === 1 ? 0 : (products.length - 1) * 5 / products.length}%)` } : {}}>
                  { flavors.map((item, index) => {
                    return (
                      <div
                        key={index}
                        className="p-product__gallery-image"
                        style={ selectedFlavors.includes(item.data.slug) ? { transform: `translateX(${products.length === 1 ? 0 : (index - 1) * 5}%)`, opacity: 1 } : { opacity: 0 }}
                      >
                        { item.image && <img src={item.image.url} /> }
                      </div>
                    )
                  })}
                </div>
              </div>

              <div className="p-product__gallery-bottles">
                { flavors.map((item, index) =>
                  <div
                    key={index}
                    className={className({ 'p-product__gallery-bottle': true, 'p-product__gallery-bottle_active': products.find(p => p.flavor === item.data.slug) })}
                    onClick={() => this.setProduct(item.data.slug, null, 'toggle')}
                  >
                    { item.image && <img src={item.image.url} /> }
                  </div>
                )}
              </div>
            </div>
          </div>
          <div className="p-product__part">
            <div className="p-product__content">
              <div className="p-product__title">
                { product.name }
              </div>
              <div className="p-product__description">
                { product.description }
              </div>
							<div className="p-product__message">
                Vanaf maandag 27/5 zal je hier je drinkklaar ontbijt kunnen bestellen.
              </div>
              <div className="p-product__attributes">
                <div className="p-product__attribute">
                  <div className="p-product__attribute-name">
                    1. { labels.step_1 }
                  </div>
                  <div className="p-product__attribute-control">
                    <div className="b-flavors">
                      <div className="b-flavors__list">
                        { flavors.map((flavor, index) =>
                          <div className="b-flavors__item" key={index}>
                            <div
                              className={className({'b-flavor': true, 'b-flavor_active': products.find(item => item.flavor === flavor.data.slug) })}
                              onClick={() => this.setProduct(flavor.data.slug, null, 'toggle')}
                            >
                              <div className="b-flavor__point" style={{ background: `linear-gradient(to bottom, ${flavor.color_shade_1} 0%, ${flavor.color_shade_1} 33.333%, ${flavor.color_shade_2} 33.333%, ${flavor.color_shade_2} 66.666%, ${flavor.color_shade_3} 66.666%, ${flavor.color_shade_3} 100%)`}}></div>
                              <div className="b-flavor__label">{ flavor.data.name }</div>
                            </div>
                          </div>
                        ) }
                      </div>
                    </div>
                  </div>
                </div>

                { products.length > 0 &&
                  <div className="p-product__attribute">
                    <div className="p-product__attribute-name">
                      2. { labels.step_2 }
                    </div>
                    { flavors.map((flavor, index) => {
                      const product = products.find(product => product.flavor === flavor.data.slug)
                      return ( product &&
                        <div className="p-product__attribute-row" key={index}>
                          <div className="p-product__attribute-label">
                            { flavor.data.name }
                          </div>
                          <div className="p-product__attribute-control">
                            <Dropdown
                              options={variations.filter(i => i.attributes.attribute_pa_flavors === flavor.data.slug)} value={product.variation}
                              onChange={(variation) => this.setProduct(product.flavor, variation, 'change')}
                            />
                          </div>
                        </div>
                      )
                    }
                    ) }
                  </div>
                }
              </div>

              { products.length > 0 &&
                <div className="p-product__controls">
                  <div className="p-product__control">
                    <div className="p-product__price">
                      &euro; { total.toFixed(2).replace('.', ',') }
                    </div>
                    <div className='p-product__flex'>
                      { total >= selectedMinAmount ?
                        <div className="p-product__delivery p-product__delivery_free">
                          { labels.free_delivery }
                        </div>
                      :
                        <div className="p-product__delivery">
                          + &euro; { window.flat_amount.toFixed(2).replace('.', ',') } { labels.delivery }
                        </div>
                      }
                      <div className="b-tooltip fade" data-title={ labels.tooltip_delivery || '' }>?</div>
                    </div>
                  </div>
                  <div className="p-product__control">
                    <a className="button button--cart" href={ window.location.pathname + `?add-to-bag=${products.map(p => p.variation).join()}`}>
                      { labels.add_to_bag_button_text }
                    </a>
                  </div>
                </div>
              }
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export const initProduct = () => {
  const selector = document.querySelector('#app-product')

  if(selector) {
    ReactDOM.render(<Product />, selector)
  }
}
