export const objectFitPolyfill = () => {

  if ('objectFit' in document.documentElement.style === false) {

    document.addEventListener('DOMContentLoaded', () => {
      
      let images = document.querySelectorAll('img.u-object-fit');

      if (images.length > 0) {
        images.forEach((el) => {

          if (el.parentNode.classList.contains('u-full')) {
            el.style.opacity = '0';
            let imageSrc = el.dataset.src || el.src;
            let styles = `
              position: absolute;
              left: 0;
              top: 0;
              right: 0;
              bottom: 0;
              background-image: url(${imageSrc});
              background-position: 50%;
              background-repeat: no-repeat;
              background-size: cover;
              `;
            let cover = document.createElement('div');
            //cover.style = styles; //ie11 error
            cover.setAttribute('style', styles);
            //el.closest('picture.u-full').appendChild(cover); //ie11 error
            el.parentNode.appendChild(cover);
          }
        });
      }
      
      let bottles = document.querySelectorAll('.p-product__gallery-bottle');
      if (bottles.length > 0) {
        bottles.forEach((el) => {
          let image = el.querySelector('img');
          let imageSrc = image.src;
          image.style.opacity = '0';

          el.setAttribute('style', `
            background-image: url(${imageSrc});
            background-position: 50%;
            background-repeat: no-repeat;
            background-size: contain;
          `);
        });
      }
    })
  }
}