import '@babel/polyfill'
import 'nodelist-foreach-polyfill'
import Swiper from 'swiper/dist/js/swiper.js'
import { ElementObserver } from 'viewprt'
import lazyLoad from './../utils/lazy'

export const stepsSliderInit = () => {
  const stepsSliderSrc = document.querySelector('.js-steps-slider');

	if ( stepsSliderSrc != null ) {

	  const stepsSlider = new Swiper(stepsSliderSrc, {
	    slidesPerView: 'auto',
	    speed: 400,
	    spaceBetween: 40,
			centeredSlides: true,
	    autoplay: {
	      delay: 4000,
	      disableOnInteraction: true
	    },
	    navigation: {
	      nextEl: '.steps-slider-next',
	      prevEl: '.steps-slider-prev'
	    },
			breakpoints: {
	      767: {
	        spaceBetween: 10,
	        centeredSlides: true
	      },
	    },
			init: true
	  });

		stepsSlider.autoplay.stop();

		stepsSlider.on('slideChange', function () {
			lazyLoad();
		});

	  if (stepsSliderSrc) {
	    stepsSlider.el.addEventListener('mouseenter', (e) => {
	      stepsSlider.autoplay.stop();
	    }, false);

	    stepsSlider.el.addEventListener('mouseleave', (e) => {
	      stepsSlider.autoplay.start();
	    }, false);
	  }


	  const stepsSliderObserver = ElementObserver(stepsSliderSrc, {
	    onEnter (element, viewport) {
				stepsSlider.autoplay.start();
	      //stepsSlider.init()
	      //console.log('Steps slider init')
	    },
	    onExit (element, viewport) {
				stepsSlider.autoplay.stop();
	      //console.log('Steps slider out viewport')
	    },
	    once: false
	  })

	}
}

export const factsSliderInit = () => {
  const factsSliderSrc = document.querySelector('.js-facts-slider')

	if ( factsSliderSrc != null ) {

	  const factsSlider = new Swiper(factsSliderSrc, {
	    slidesPerView: 1,
	    speed: 400,
			direction: 'vertical',
	    loop: true,
			simulateTouch: false,
			allowTouchMove: false,
	    autoplay: {
	      delay: 4000
	    }
	  });

		factsSlider.autoplay.stop();

	  const factsSliderObserver = ElementObserver(factsSliderSrc, {
	    onEnter (element, viewport) {
				factsSlider.autoplay.start();
	      //console.log('Facts slider init')
	    },
	    onExit (element, viewport) {
				factsSlider.autoplay.stop();
	      //console.log('Facts slider out viewport')
	    },
	    once: false
	  })

	}
}

export const flavorsSliderInit = () => {
  const flavorsSliderBottlesSrc = document.querySelector('.js-slider-flavors-bottles')
  const flavorsTabs = document.querySelectorAll('.js-slider-flavors-tabs .tab')
  const flavorsSliderContents = document.querySelectorAll('.slider-flavors-content .slider-flavors-content__slide')
  const flavorsShades = document.querySelectorAll('.shades')

	if ( flavorsSliderBottlesSrc != null ) {

	  const flavorsSliderBottles = new Swiper(flavorsSliderBottlesSrc, {
	    slidesPerView: 1,
	    speed: 600,
	    direction: 'horizontal',
	    simulateTouch: true
	  })

	  flavorsTabs.forEach((flavorsTab, index) => {
	    flavorsTab.addEventListener('click', function () {
	      flavorsSliderBottles.slideTo(index)
	      flavorsTab.classList.add('is-active')
	    })
	  })

	  flavorsSliderBottles.on('slideChange', function () {
	    flavorsTabs.forEach((item, index) => {
	      item.classList.remove('is-active')
	      if (flavorsSliderBottles.activeIndex === index) {
	        item.classList.add('is-active')
	      }
	    })
	    flavorsShades.forEach((item, index) => {
	      item.classList.remove('is-active')
	      if (flavorsSliderBottles.activeIndex === index) {
	        item.classList.add('is-active')
	      }
	    })
	    flavorsSliderContents.forEach((item, index) => {
	      item.classList.remove('is-active')
	      if (flavorsSliderBottles.activeIndex === index) {
	        item.classList.add('is-active')
	      }
	    })
	    lazyLoad();
	  })

	}
}
